:root{
  width: 100%;
  
}
header
{
  background-color: beige;
  text-align: center;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
}
.items-ul
{
  padding: 30px;
  margin: 0 auto;
  width: fit-content;
}
.items
{
  text-decoration: none;
  margin-bottom: 20px;
  background-color:rgb(226, 242, 255);
  width:fit-content;
  padding: 10px;
  border-radius: 11px;
 
}
li{
  list-style: none;
}
#trashicon
{
  float: right;
}
#trashicon:hover{
 color: red;
 border: none;
}
#empty
{
background-color: aliceblue;
width: fit-content;
margin: 50px auto;
padding: 20px;
border-radius: 30px;
align-items: center;
text-align: center;
color: red;
}
.items input
{
  margin-right: 20px;
  width: 20px;
  height: 20px;
  
}
.items label
{
  margin-right: 20px;
}
.addform
{
  margin: 0 auto;
  background-color: rgb(227, 225, 225);
  width: fit-content;
  padding: 20px;
  border-radius: 10px;
}
.addform input
{
  height: 27px;
  font-size: 20px;
  padding: 7px;
  margin-right: 20px;
  margin-top: 10px;
  border-top: none;
  border-left: none;
  border-right: none;
  border-radius: 11px;

}
.addform button
{
  height: 36px;
  padding: 7px;
  width: 50px;
  font-size:20px ;
  background-color: green;
  color: white;
  border-radius: 10px;
  border: none;
  line-height: -2px;
  cursor: pointer;
}
main{
  margin-top: 130px;
   font-size: 30px;
   text-decoration: none;
   transition: 0.5s ease;

}
footer
{
   background-color: bisque;
   text-align: center;
   font-size: 20px;
   position: fixed;
   left: 0;
   bottom: 0;
   height: 30px;
   width: 100%;
   padding: 10px;
   
}